/**  */
$color-white: #fff;
$color-dark: rgba(0, 0, 0, 0.8);
// 
$color-light-gray: #BDBDBD;

// 
$primary-color: #FF7C02;
// 
$primary-variant-color: #FFF8F2;
// 
$secondary-color: #C16E00;
// 
$secondary-variant-color: #e6f3f7;
// 
$third-color: #f3202e;
// 
$third-variant-color: #eed7ed;
// danger
$danger-color: #E73D4A;

/**  */
$font-sz: (
  xl: 30px,
  lg: 18px,
  md: 16px,
  sm: 14px,
  xs: 12px,
);

$font-weight: (
  fatty: 600,
  normal: normal,
);

/**  */
/* base btn start */
$btn: (
  border-radius: 7px,
  shadow: 1px 1px 2px 0 rgba(21, 28, 42, 0.17),
);
$btn-height: (
  lg: 40px,
  md: 35px,
  sm: 30px,
);
$btn-width: (
  xl: 200px,
  lg: 150px,
  md: 120px,
  sm: 90px,
  xs: 60px,
);
/* base btn end */

/** base card start*/
$base-card: (
  background-color: $color-white,
  box-shadow: none,
  padding: 0,
  border: 0,
  border-radius: 6px,
);

$base-card-header: (
  align-self: center,
  font-size: map-get($font-sz, lg),
  font-weight: map-get($font-weight, fatty),
  padding: 0 15px,
);

$base-card-content: (
  padding: 10px 15px,
);
/** base card end*/

/* base table start */
$base-table: (
  // hover 
  hover-background-color: darken($color-light-gray, 15%),
  // 
  even-col-color: $color-light-gray,
  // 
  radius: 7px,
  // 
  shadow: 1px 2px 4px #d5d5d5,
  // thtd 
  padding: 4px 8px,
  // thtd flex table 
  mo-padding: 10px 8px,
  // pxflex table 
  break-point: 991px,
  // border-bottomflex table 
  mo-border-bottom-width: 1px
);

/* base tab start */
$base-tab: (
  // (merc-tab  mercType )
  tab-color: $primary-color,
  // tab 
  tab-border-width: 1px,
  // content 
  content-border-width: 10px,
  // 
  radius: 7px,
  // 
  font-size: map-get($font-sz, lg)
);
/* base tab end */

/* slide image start */
$slide-image: (
  image-background-color: $color-light-gray,
  focus-border: 2px solid $primary-color
);
/* slide image end */
